.call-to-action {
  padding: 130.5px 0px;
  margin-bottom: 118.45px;
  text-align: center;

  background-image: $linear-gradient, url('../images/call-to-action.jpg');
  height: auto;
  background-size: cover;
}

.call-to-action__header {
  font-family: 'Poppins';
  font-style: normal;
  font-weight: 600;
  font-size: 32px;
  line-height: 1.375;
  margin-bottom: 16px;

  max-width: 462px;
  margin-left: auto;
  margin-right: auto;

  color: #ffffff;
}

.call-to-action__button {
  font-family: 'Poppins';
  font-style: normal;
  font-weight: 600;
  font-size: 14px;
  line-height: 3.35;

  width: 152px;
  height: 56px;

  padding: 4.5px 16px;
  color: #ff6800;
  background: #ffffff;
  border-radius: 15px;
  border: 0;

  cursor: pointer;

  transition-property: box-shadow;
  transition-duration: $transition-duration;
  transition-timing-function: $timing-function;

  &:hover,
  &:focus {
    box-shadow: inset 0 0 10px 5px rgb(0 0 0 / 30%);
  }
}
